import { useFormik } from "formik";
import { basicSchema } from "../schemas";
import "../componants/CSS/SignIn.css";
import { Link } from 'react-router-dom';
import { Navigate  } from 'react-router-dom';
import {connect} from 'react-redux'
import React,{useState} from 'react'
import { login } from '../actions/auth';
import axios from 'axios';

// const onSubmit = async (values, actions) => {
//   await new Promise((resolve) => setTimeout(resolve, 1000));
//   actions.resetForm();
// };


const Login = ({ login,isAuthenticated }) => {
  const [formData,setFormData]=useState({
    email:'',
    password:''
  });

  const {email,password}=formData;

  const onChange = e => setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit=e =>{
    e.preventDefault();
    login(email,password)
  }

//   const {
//     values,
//     errors,
//     touched,
//     isSubmitting,
//     handleBlur,
//     handleChange,
//     handleSubmit,
//   } = useFormik({
//     initialValues: {
//       firstName: "",
//       lastName: "",
//       email: "",
//       age: "",
//       password: "",
//       confirmPassword: "",
//     },
//     validationSchema: basicSchema,
//     onSubmit,
//   });

//   console.log(errors);
  
  if (isAuthenticated) {
    return <Navigate  to='/' />
  }

  return (
    <div className=" px-3 py-3 my-5 shadow mx-auto bg-light" id="formHolder">
      <form
        onSubmit={e=>onSubmit(e)}
        autoComplete="off"
        className="regstrationForm"
      >
        <label htmlFor="email">Email</label>
        <input
          value={email} //values.email
          // onChange={handleChange}
          onChange={e=>onChange(e)}
          id="email"
          type="email"
          name="email"
          placeholder="Enter your email"
        //   onBlur={handleBlur}
          
        //   className={errors.email && touched.email ? "input-error" : ""}
        />
        {/* {errors.email && touched.email && (
          <p className="error">{errors.email}</p>
        )} */}

        <label htmlFor="password">Password</label>
        <input
          id="password"
          type="password"
          name="password"
          placeholder="Enter your password"
          // value={values.password}
          value={password}
          // onChange={handleChange}
          onChange={e=>onChange(e)}
        //   onBlur={handleBlur}
        //   className={errors.password && touched.password ? "input-error" : ""}
        />
        {/* {errors.password && touched.password && (
          <p className="error">{errors.password}</p>
        )} */}

        <button 
        // disabled={isSubmitting} 
        type="submit">
          Sign in
        </button>
        <p className=" text-center">
          Or if your are not a member <Link to="/signup">Sign up</Link>
        </p>
        
      </form>
      <p className=" text-center">
          Forget Your Password ? <Link to="/reset-password">Reset Password</Link>
      </p>
    </div>
  );
};


const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps,{ login })(Login);
